*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.login{
    padding: 30px;
    width: 300px;
    height:300px;
    background-color: rgba(255, 255, 255, 0.256);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    border-radius: 10px;
}
label{
    display: flex;
    flex-direction: column;
}


